import React, {Component} from 'react';

// import React from 'react';

import './header.scss';
import './media-header.scss';
import logo from '../header/img/logo.png';
import {FaBars} from 'react-icons/fa';
import { BrowserRouter as Router, Route, Link } from 'react-router-dom';

export default class Header extends Component{
    state = {
        isOpen: false
    };

    hangleToggle = () => {
        this.setState({isOpen: !this.state.isOpen});
    };

    render () {
        return (
            <div className="MainHeader">
                <div className="container">
                    <div className="row">
                        <div className="menu menu-812px-main d-flex align-items-center justify-content-between">
                            <Router>
                                <Link to="/">
                                    <img className="logo img-responsive" src={logo} alt=""/>
                                </Link>
                                <button
                                    className="nav-btn"
                                    type="button"
                                    onClick={this.hangleToggle}>
                                    <FaBars className="nav-icon"/>
                                </button>
                                <nav className={this.state.isOpen?"nav-links show-nav":"nav-links"}>
                                    <ul>
                                        <li><Link to="/">Case Studies</Link></li>
                                        <li><Link to="/services">Services</Link></li>
                                        <li><Link to="/expertise">Expertise</Link></li>
                                        <li><Link to="/about">About</Link></li>
                                        <li><Link to="/contact">Contact us</Link></li>
                                    </ul>
                                </nav>
                            </Router>
                        </div>
                    </div>
                </div>
            </div>
        )
    };
};

// export default Header;